.cart-empty {
  padding-top: 56px;
  text-align: center;
}

.Cart .CartItemsTable,
.Cart .CartTotal,
.Cart .checkout-btn {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 5px;
}

.Cart {
  padding-top: 56px;
  margin: 0.2rem;
  display: grid;
  grid-template-columns: 1fr;
}

.Cart .box-qty {
  min-width: 40px;
  width: 50%;
}

.CartItems {
  border: 1px solid #ccc;
  margin: 0.1rem;
}

.CartItems .cart-item {
  display: grid;
  padding: 0.5em 0.2rem;
  grid-template-columns: 1fr 5fr 1fr 1fr;
}

.CartItems .cart-item > div {
  padding: 0 2px;
}

.CartItems .cart-image > img {
  width: 100px;
  max-width: 150px;
}

.CartItems .cart-item-description {
  padding: 0 0.5rem;
}

.CartItems .cart-item-description > span {
  display: block;
  margin-bottom: 5px;
}

.CartItems .cart-item-title {
  font-size: 1rem;
  color: #00f;
}

.CartItems .cart-item-description {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.CartItems .cart-item-delete {
  margin-top: auto;
  margin-bottom: 5px;
}

.CartItems .cart-item-delete-text {
  font-size: 0.8rem;
}

.CartItems .cart-item-delete-text:hover {
  cursor: pointer;
}

.CartItems .cart-item-price {
  font-weight: bold;
  color: #cf3b00;
}

.CartItems .cart-item-quantity form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.CartItems .cart-item-quantity form input {
  margin-bottom: 5px;
}

.CartItems .cart-item-quantity form button {
  font-size: 0.7rem;
  background: -webkit-gradient(linear, left top, left bottom, from(#f7dfa5), to(#f0c14b));
  background: -webkit-linear-gradient(top, #f7dfa5, #f0c14b);
  background: -o-linear-gradient(top, #f7dfa5, #f0c14b);
  background: linear-gradient(to bottom, #f7dfa5, #f0c14b);
  border: 1px solid black;
  border-radius: 3px;
  padding: 3px;
}

.CartItems .cart-item-quantity form button:hover {
  cursor: pointer;
}

.CartSummary {
  border: 1px solid #ccc;
  margin: 0.1rem;
  background: #eee;
  min-width: 200px;
}

.CartSummary .order-summary {
  margin: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  text-align: center;
}

.CartSummary .order-summary > span {
  display: block;
}

.CartSummary .order-summary-title {
  font-size: 1.1rem;
  font-weight: bold;
}

.CartSummary .order-summary-price {
  color: #cf3b00;
  font-weight: bold;
}

.CartSummary .order-summary-checkout-button {
  display: -ms-flexbox;
  display: flex;
  margin-top: 1rem;
}

.CartSummary .order-summary-checkout-button > a {
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 0.8rem;
  background: -webkit-gradient(linear, left top, left bottom, from(#f7dfa5), to(#f0c14b));
  background: -webkit-linear-gradient(top, #f7dfa5, #f0c14b);
  background: -o-linear-gradient(top, #f7dfa5, #f0c14b);
  background: linear-gradient(to bottom, #f7dfa5, #f0c14b);
  border: 1px solid black;
  border-radius: 3px;
  width: 100%;
}

.CartSummary .order-summary-checkout-button > a:active {
  -webkit-transform: translateY(1px);
      -ms-transform: translateY(1px);
          transform: translateY(1px);
}

.CartSummary .order-summary-checkout-button > button:hover {
  cursor: pointer;
}

.CartSummary a {
  text-decoration: none;
}

.tenPlus {
  border-top: 1px solid #999;
}

@media (min-width: 768px) {
  .Cart {
    grid-template-columns: 7fr 2fr;
  }

  .CartItems .cart-item-title {
    font-size: 1.1rem;
  }
}



