nav {
  width: 100%;
  height: 52px;
  top: 0;
  background-color: #343a40;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.5em 1em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.fixed-top {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 9999;
}

nav .navbar-brand {
  font-size: 1.5em;
  margin-left: 1rem;
}

nav .navbar-menu {
  display: none;
}

nav .nav-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

nav a {
  color: #fff;
  text-decoration: none;
}

nav .nav-item a {
  display: block;
  padding: 0.5em 1em;
}

nav .navbar-collapse {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-preferred-size: auto;
      flex-basis: auto;
}

nav ul {
  float: left;
}

.navbar-icon-top .navbar-nav .nav-link > .fa {
  position: relative;
  width: 36px;
  font-size: 24px;
}

.navbar-icon-top .navbar-nav .nav-link > .fa > .badge {
  font-size: 0.75rem;
  position: absolute;
  top: -10px;
  right: 0;
  font-family: sans-serif;
}

.navbar-icon-top .navbar-nav .nav-link > .fa {
  top: 3px;
  line-height: 12px;
}

nav .cart {
  margin-left: auto;
}

nav .cart .nav-item a {
  padding: 0;
}

@media (min-width: 545px) {
  nav .navbar-menu {
    display: block;
  }

  nav .navbar-nav {
    display: -ms-flexbox;
    display: flex;
    list-style-type: none;
    margin: 0;
    margin-right: auto;
  }
}



